<template>
    <div class="iss-view">
      <div class="text">{{viewList.name}}</div>
      <div class="question">
          Q<span class="index">{{viewList.level}}</span>:{{viewList.question}}
      </div>
      
      <div class="select-btn" v-if="viewList.questionType == 'M'">
          <div v-for="(item,i) in viewList.questionData" class="options" :class="selectIndex && selectIndex.indexOf(i)>-1?'active':''" @click="selectFilter(i)">
            <i></i> {{item.name}} 
          </div>
      </div>
        <div class="btn" v-else>
          <div v-for="(val,i) in viewList.questionData" @click="resultEmit(val,i)">{{val.name}} </div>
      </div>
      <div class="submit lz-isIndex" v-if="viewList.questionType == 'M'" @click="selectResult">
        确认
      </div>
   </div>
</template>

<script>
    export default {
      props: {
        viewList: Object,
        index: Number,
      },
      data() {
        return {
          selectIndex:''
        };
      },
      methods: {
        resultEmit(item, i) {
          this.$emit("questionApi", item, i);
        },
        //数组过滤
        selectFilter(i){
          if(!this.selectIndex){
            this.selectIndex = i+""
          }else if(this.selectIndex.indexOf(i) >-1){
            let arr = this.selectIndex.split(",")
            let index = arr.indexOf(i.toString());
            arr.splice(index,1)
            this.selectIndex = arr.join(",")
            console.log(arr)
          }else{
            this.selectIndex = this.selectIndex + ","+i
          }
        },
        selectResult(){
          if(!this.selectIndex)return this.$popup.msg("至少选择一项")
          this.$emit("questionApi", {}, this.selectIndex,true);
        }
      },
      mounted: function() {},
      created() {},
      watch: {
        viewList(newVal) {
          console.log(newVal);
        }
      }
    };
</script>

<style lang="scss">
.iss-view {
  .text {
    display: inline-block;
    padding: 15px 0 0 15px;
    font-weight: bold;
  }

  .question {
    padding: 15px;
    line-height: 1.5;
    border-bottom: 1px solid #ccc;
  }
  .btn {
    margin-top: 30px;
    margin-bottom: 50px;
    color: #fff;
    div {
      padding: 10px;
      margin: 10px auto;
      width: 80%;
      background: $baseColor;
      text-align: center;
      border-radius: 7px;
      box-sizing: content-box;
    }
  }
  .select-btn {
    margin-top: 30px;
    margin-bottom: 30px;
    color: #fff;
    .options{
          display: block;
          width: 85%;
          margin: 15px auto;
          height: r(85);
          line-height: r(85);
          font-size: r(36);
          border: r(2) solid #dcdcdc;
          border-radius: 4px;
          color: #333;
          padding-left: r(30);
          color: $baseColor;
          border-color: $baseColor;
          i{
            display: inline-block;
            width: r(40);
            height: r(40);
            border-radius: 100%;
            vertical-align: middle;
            border: r(2) solid $baseColor;
            background-color: #fff;
            margin-right: r(30);
            position: relative;
            
            &::before{
              content: "";
              position: absolute;
              width: r(24);
              height: r(24);
              left: 50%;
              top: 50%;
              transform: translate(r(-12),r(-12));
              border-radius: 100%;
            }
          }
          &::before{
            content: "";
            display: inline-block;
            position: relative;
            height: 100%;
            width: 1px;
            left: 0;
            vertical-align: middle
          }
          &.active{
            background-color: rgba($color: $baseColor, $alpha: .2);
            box-shadow: 0 1px 5px rgba($color: $baseColor, $alpha: .5);
            i{
              border-color: $baseColor;
              border: r(4) solid $baseColor;
               &::before{
                 background-color: $baseColor;
               }
            }
          }
        }
  }
  .submit{
    margin-top: 0;
    margin-bottom: 20px;
  }
}
</style>


